/**
* couleur :
* #1d1d1e : background
code couleur:
#0098da (bleu)
#a8cf45 (vert jaune)
#1d1d1e (black grey)
#e6e6e8 (gris)
*/

html {
   scroll-behavior: smooth;
}

.Home {
   text-align: center;
}

/*Landing page section*/
.goToTop {
   position: fixed;
   bottom: 16px;
   z-index: 5;
   right: 24px;
   height: 46px;
   width: 46px;
   border-radius: 64px;
   font-size: 42px;
   padding: 18px;
   background-color: white;
   color: #001822;
}

.Home .landing {
   display: flex;
   justify-content: center;
   align-items: center;
   min-height: 940px;
   background: url('../../assets/images/background_landing.jpg') no-repeat fixed
      center bottom;
   background-size: cover;
}

.landing .content_landing {
   width: 50%;
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
}

.landing .content_landing .button_utils button {
   margin: 10px 16px;
}

/*section page*/
.section {
   margin: 32px 0px;
   position: relative;
}

.brand {
   margin: 140px 54px 0px 54px;
   font-size: 2.4vmin;
}

.section .brand_one .illustration {
   position: relative;
}

.image_brand_one {
   border-radius: 32px;
}

.brand_two {
   margin: 180px 0px 0px 0px;
}

.brand_two .description_about {
   display: flex;
   justify-content: center;
   align-items: center;
   min-height: 580px;
   background: url('../../assets/images/about_bg.png') no-repeat fixed center
      bottom;
   background-size: cover;
}

.brand_two .description_about p {
   width: 68%;
   text-align: center;
   font-size: 2.6vmin;
}

.brand_three .question_faq .faq_content {
   background-color: #e6e6e8;
   color: #001822;
   border-radius: 18px;
   padding: 5px 0px;
   margin: 8px 0px;
   text-align: center;
}

/*Responsive*/
/*extra small*/
@media only screen and (min-width: 100px) {
   .section .brand_one .illustration {
      height: 8vh;
      top: -60px;
      left: -56px;
   }

   .illustration .background_ph {
      display: block;
      position: absolute;
      height: 26vmin;
      width: 26vw;
   }

   .illustration .disque_ph {
      display: block;
      position: absolute;
      height: 28vmin;
      width: 26vw;
      z-index: 2;
      top: 0px;
      left: 0px;
   }

   .illustration .album_ph {
      display: block;
      position: absolute;
      height: 24vmin;
      width: 23vw;
      top: 0;
      left: 8px;
      z-index: 3;
      transform: rotate(-16deg);
   }

   .section .brand_one {
      display: flex;
      flex-direction: column;
      align-items: center;
   }

   /*FAQ*/
   .brand_three {
      display: flex;
      flex-direction: column;
      align-items: center;
   }
   .brand_three .question_faq {
      display: flex;
      flex-direction: column;
   }
   .brand_three .question_faq .faq_content {
      width: 100%;
   }
}

/*small*/
@media only screen and (min-width: 600px) {
   .section .brand_one .illustration {
      height: 300px;
      top: 0px;
      left: -80px;
   }

   .illustration .background_ph {
      display: block;
      position: absolute;
      height: 26vmin;
      width: 24vw;
   }

   .illustration .disque_ph {
      display: block;
      position: absolute;
      height: 28vmin;
      width: 26vw;
      z-index: 2;
      top: 0px;
      left: -8px;
   }

   .illustration .album_ph {
      display: block;
      position: absolute;
      height: 24vmin;
      width: 22vw;
      top: 0px;
      left: 0px;
      z-index: 3;
      transform: rotate(-16deg);
   }
}

/*medium*/
@media only screen and (min-width: 900px) {
   .section .brand_one .illustration {
      height: 400px;
   }

   .illustration .background_ph {
      display: block;
      position: absolute;
      height: 36vh;
      width: 28vw;
   }

   .illustration .disque_ph {
      display: block;
      position: absolute;
      height: 44vh;
      width: 28vw;
      z-index: 2;
      top: -28px;
      left: 0px;
   }

   .illustration .album_ph {
      display: block;
      position: absolute;
      height: 44vh;
      width: 22vw;
      z-index: 3;
      transform: rotate(-16deg);
   }
   .section .brand_one {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
   }

   .brand_one .description_brand {
      position: relative;
      top: -62px;
      height: 60vh;
      width: 40vw;
   }

   .description_brand .info_description_brand {
      position: relative;
      top: -82px;
      left: 116px;
      width: 100%;
      text-align: left;
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      justify-content: space-between;
   }
}

/*desktop*/
@media only screen and (min-width: 1400px) {
   .section .brand_one .illustration {
      height: 400px;
   }

   .illustration .background_ph {
      display: block;
      position: absolute;
      height: 42vmin;
      width: 20vw;
      left: 32px;
   }

   .illustration .disque_ph {
      display: block;
      position: absolute;
      height: 48vmin;
      width: 24vw;
      z-index: 2;
      top: -24px;
      left: 0px;
   }

   .illustration .album_ph {
      display: block;
      position: absolute;
      height: 42vmin;
      width: 21vw;
      top: 0px;
      left: 32px;
      z-index: 3;
      transform: rotate(-16deg);
   }

   .brand {
      margin: 180px 54px 0px 54px;
   }

   .section .brand_one {
      justify-content: space-around;
   }

   .description_brand .info_description_brand {
      position: relative;
      top: -122px;
      left: 136px;
      width: 100%;
   }

   /*FAQ*/
   .brand_three {
      display: flex;
      flex-direction: column;
      align-items: center;
   }
   .brand_three .question_faq {
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
   }
   .brand_three .question_faq .faq_content {
      width: 30%;
   }
}
