/*CAR ONE ITEM RESULT*/
.container_result .one_item_result {
   margin: 16px 0px;
   background-color: white;
   color: #1d1d1e;
   padding: 8px 0px;
   width: 56vmin;
   min-height: 100px;
   display: flex;
   flex-direction: column;
   align-items: center;
}

.container_result .one_item_result h4 {
   margin: 0;
   font-size: 1.7vmin;
   font-weight: 700;
}

.container_result .one_item_result p {
   font-size: 1.3vmin;
}

.container_result .one_item_result .bouttons {
   display: flex;
   width: 40%;
   flex-wrap: wrap;
   justify-content: space-evenly;
}

.container_result .one_item_result .bouttons button {
   cursor: pointer;
   width: 10vmin;
   height: 40px;
   padding: 8px;
   text-align: center;
   border-radius: 4px;
   font-size: 1.4vmin;
   font-weight: bold;
   color: white;
   background-color: #1d1d1e;
   border: none;
}
